                            </div>
                        </div>
                    </div>
                </div>
            </div>


        </div>

        <div class="accordion accordion-flush" :id="'accordionFlushExample_' + item.articleId" v-if="IsAccordionShow"
            v-for="(item, index) in HelpArticleList" :key="index">
            <div class="accordion-item">

                <ul>
                    <li class="accordion-header" :id="'flush-headingOne_' + item.articleId" style="margin-left:15px;">
                        <a class="collapsed" data-bs-toggle="collapse"
                            :data-bs-target="'#flush-collapseOne_' + item.articleId" aria-expanded="false"
                            :aria-controls="'flush-collapseOne_' + item.articleId"
                            @click="GetArticleContent(item.articleId)">{{ item.articleName }}</a>
                    </li>

                </ul>

                <div class="col-12 p-0">
                    <div :id="'flush-collapseOne_' + item.articleId" class="accordion-collapse collapse p-0 border"
