</template>

<script>
import Search from "../../../src/views/HelpAndGuide/Search.vue";
import HelpDataService from '../../services/DataService';


import SimpleTypeahead from 'vue2-typeahead';

export default
    {
        name: 'HelpArticles',
       
        components: {
            Search,
            SimpleTypeahead
        },
        data() {
            return {
                HelpArticleList: [],
                ArticleNameList: [],
                Article: '',
                ArticlesContent: '',
                IsPopularSearchShow: true,
                IsDataNotFoundShow: false,
                IsAccordionShow: false,
                isLoading: false,               
                Path: Boolean,
                NewValue: Boolean

            }
        },
        created: function () {       
                       
                  
            this.ArticleList();

        },
        methods: {

            ArticleList: function () {
                var vm = this;
               
                vm.isLoading = true;
                var params = `userType=SA&articleType=HelpArt&moduleId=`;
                HelpDataService.GetArticleList(params).then(function (response) {
                    
                    if (response.data.length > 0) {

                        response.data.forEach((value, index) => {

                            vm.ArticleNameList.push(value.articleName)
                            vm.isLoading = false;
                        })
                    }

                });
            },
            SearchData: function () {

                var vm = this;
                vm.isLoading = true;
                vm.Article = document.getElementById("typeahead_id").value;

                var params = `search=${vm.Article}&userType=SA&articleType=HelpArt&moduleId=`;
                HelpDataService.GetArticleList(params).then(function (response) {

                    if (response.data.length > 0) {
                        vm.HelpArticleList = response.data;
                        vm.IsPopularSearchShow = false;
                        vm.IsAccordionShow = true;
                        vm.isLoading = false;
                    }
                    else if (response.data.length == 0) {

                        vm.IsPopularSearchShow = false;
                        vm.IsDataNotFoundShow = true;
                        vm.IsAccordionShow = false;
                    }

                });
            },
            ClearData: function () {
                document.getElementById("typeahead_id").value = '';
                this.IsPopularSearchShow = true;
                this.IsDataNotFoundShow = false;
                this.IsAccordionShow = false;

            },
            GetArticleContent: function (ArticlesId) {
                var vm = this;
                var params = `ArticleId=${ArticlesId}&articleType=HelpArt`;
                HelpDataService.GetArticleDetailById(params).then(function (response) {
                    if (response.data != null) {
                        vm.ArticlesContent = response.data.article_content;
                    }
                });
            },
        },
    };
</script>

<style>
.ArticleList {
    max-height: 400px;
    overflow-y: auto;
    position: relative;
}

#typeahead_id {
    width: 100%;
}


#typeahead_id_wrapper .simple-typeahead-list {
    text-align: left;
    width: 100%;

}
</style> 